'use client'

interface SidebarProps {
  isOpen: boolean
}

export default function Sidebar({ isOpen }: SidebarProps) {
  return (
    <aside
      className={`fixed md:static z-50 top-0 left-0 h-full w-64 bg-blue-800 text-white p-4 transition-transform duration-300
      ${isOpen ? 'translate-x-0' : '-translate-x-full'} md:translate-x-0`}
    >
      <h2 className="text-lg font-bold mb-4">Menu</h2>
      <ul className="space-y-4">
        <li>Dashboard</li>
        <li>Rendez-vous</li>
        <li>Profil</li>
      </ul>
    </aside>
  )
}
